.cs_checkbox {
    display: block;
    position: relative;
    padding-left: 25px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
      }
      .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 16px;
        width: 16px;
        background-color: transparent;
        border: 1px solid $border_color_3;
      }
  }

  .section_tittle{
      text-align: center;
      h2{
        font-size: 36px;
        margin-bottom: 25px;
        line-height: 33px;
          @media #{$tab}{
            margin-bottom: 15px;
            font-size: 25px;
          } 
          @media #{$tab}{
            font-size: 30px;
          }
      }
      p{
          margin-bottom: 72px;
          @media #{$tab}{
              margin-bottom: 40px;
          }
          @media #{$medium_device}{
              margin-bottom: 50px;
          }
      }
  }
  /* Create the checkmark/indicator (hidden when not checked) */
  .cs_checkbox:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
  .cs_checkbox input:checked ~ .checkmark:after {
    display: block;
    left: 6px;
    top: 1px;
    width: 5px;
    height: 9px;
    border: solid $text_color;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    content: "";
  }
