        .switch {
            -webkit-appearance: none;
            background-color: transparent;
            padding: 14px 27px !important;
            margin-right: 0;
            border: 1px solid $border_color_3;
            &:after{
                top: 4px;
                width: 20px;
                height: 20px;
            }
        }
        .switch:checked::after {
            background-color: $text_color !important;
            left: 4px;
        }
        .switch:checked {
