// file upload 
.file_upload{
    border: 1px solid #eee1e2 !important;
    padding: 9px 24px;
    border-radius: 3px;
    label{
        margin-bottom: 0 !important;
    }
    .form-group{
        padding: 0;
        margin-bottom: 0;
    }
    .btn_file_upload {
        color: $white;
        padding: 0;
        height: 30px;
        line-height: 30px;
        border: 0;
        display: inline-block;
        &:hover, 
          &:focus {
            color: lighten(#555, 20%);
            border-color: lighten(#555, 20%);
          }
      }
      
      /* input file style */
      
    .input-file ,.input-file2 {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
        + .js-labelFile, + .js-labelFile2 {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            padding: 0 10px;
            cursor: pointer;
        }
    }
